.resume {
  font-family: "Computer Modern Bright", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.container {
  margin-top: 3rem;
  margin-bottom: 8rem;
}

header {
  margin-bottom: 3rem;
}

section {
  margin-bottom: 2.25rem;
}

section.minor-section {
  margin-bottom: 1.65rem;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.45rem;
  margin-bottom: .7rem;
}

h3 {
  font-size: 1.25rem;
  margin-top: 1.3rem;
}

a {
  color: #000 !important;
}

ul.loose-list li {
  margin-bottom: .6rem;
}

strong {
  font-family: "Computer Modern Bright Semibold", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 500;
}

.opaque-on-hover:hover {
  opacity: 1.0 !important;
}

@media print {
  html {
    font-size: 12px;
  }

  .container {
    margin-top: 2rem;
    margin-bottom: 0;
  }
}
